import React, {Component} from 'react';
import ScrollableAnchor from 'react-scrollable-anchor';

import './ComitesIntro.scss';

class ComitesIntro extends Component {

	render () {
		return (
			<div className="ComitesIntro Section">
				<p>
					Participez  l'organisation de la fte  Macron!
					Pour cela, rejoignez le comit le plus proche de chez vous en utilisant <a href="#section-trouver">la
					carte interactive</a> !
				</p>
				<p>
					Pas encore de comit cr dans votre ville ? Pas de panique, les comits locaux cest vous !
					Pour vous lancer, vous avez  votre disposition <a href="#section-outils">la bote  outil</a> !
					Elle contient des ressources et des liens utiles  la cration de votre comit local.
				</p>
			</div>
		);
	}
}

export default ComitesIntro;




